import { RM } from '@speedlo/tools'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormDropdown } from '../../../components/forms/FormDropdown'
import {
  QMenuCategoryOriginsOrigins,
  useQMenuCategoryOrigins,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'

export const CategoryOriginsField: React.FC = () => {
  const { data, loading } = useQMenuCategoryOrigins()
  return (
    <IconLine label={i18n.t`cat. origins`} labelWidth={7}>
      <FormDropdown<QMenuCategoryOriginsOrigins>
        fieldName="origins"
        isMulti={true}
        isLoading={loading}
        isSearchable={true}
        options={loading ? [] : (data.origins as any)}
        getOptionLabel={RM.prop('code')}
        getOptionValue={RM.prop('enum')}
      />
    </IconLine>
  )
}
