import { SelectAll } from '@material-ui/icons'
import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormDropdown } from '../../../components/forms/FormDropdown'
import {
  QMenuWareUnitsWareUnits,
  useQMenuWareUnits,
} from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'

interface IProps {}

type TWareUnits = QMenuWareUnitsWareUnits

export const StockWareUnitField: React.FC<IProps> = selectProps => {
  const { data, loading } = useQMenuWareUnits()

  return (
    <IconLine icon={SelectAll} labelWidth={7}>
      <FormDropdown<TWareUnits>
        options={data.wareUnits}
        isLoading={loading}
        getOptionLabel={RM.prop('code')}
        getOptionValue={RM.prop('enum')}
        placeholder={i18n.t`Choose unit`}
        fieldName="wareUnit"
        width={12}
      />
    </IconLine>
  )
}
