import { useXFieldState } from '@speedlo/xform'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

interface IProps {
  fieldName: string | string[]
  className?: string
  children?: never
}

export const FormDisplayValue: React.FC<IProps> = ({
  fieldName,
  className,
}) => {
  const field = useXFieldState<string>(fieldName)
  return useObserver(() => <span className={className}>{field.value}</span>)
}
