import React from 'react'

import { IconLine } from '../../../atoms/IconLine'
import { FormFlatSingleSelect } from '../../../components/forms/FormFlatSingleSelect'
import { useQMenuVats } from '../../../graph/generated'
import { useSettingsBranch } from '../../../hooks/useSettingsBranch'
import { i18n } from '../../../i18n'

interface IProps {
  fieldName: FieldName
}

export const RecipeVatSelectField: React.FC<IProps> = ({ fieldName }) => {
  const branchId = useSettingsBranch()
  const { data, loading } = useQMenuVats(
    { branchId },
    { fetchPolicy: 'cache-first' },
  )

  if (loading) {
    return null
  }

  return (
    <IconLine label={`${i18n.t`VAT`}:`}>
      <FormFlatSingleSelect
        fieldName={fieldName}
        items={data.vats}
        formatItem={({ vat }) => `${vat}%`}
      />
    </IconLine>
  )
}
